@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Codystar&family=Homemade+Apple&display=swap);

html,body {
  margin: 0;
  background-color: black;
  font-family: 'Fredoka One', cursive;
  
}

/* MENU STYLES */
.menu{
  background-color: rgb(156,190,139);
  box-shadow: 0px 20px 30px rgb(53, 53, 40);   
  display: inline-flex;
  position: fixed;
  z-index: 4;
  justify-content: space-evenly;
  width: 100vw;
  height:10vh;
  margin:0;
 
}
.contactInfo a:link{
  font-size: 2vmin;
  text-decoration: none;
  color:black;
  padding:2vmin;
  align-content: center;

 
}



.option, .option a{
  display: block;
  text-align: center;
  
}


.icon, .option a{
  color:black;
  font-size: 6vh;

}

.description{
  font-size: 2vh;
  color:rgb(63,83,53);
  margin:0;

}
.nameLink {
    color:white;
    font-size: 5vmin;
    text-decoration: none;

}
.name p{
    margin:0px;
    text-shadow: 0px 0px 5px rgb(53, 53, 40);
    text-align: center;
}


.mainPage{
  background-image: url(/static/media/palms_edited.625bcb94.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  height:100vh;
  width: 100vw;

}
.card{

  margin:3vmax 3vw;
  box-shadow: 5px 5px rgb(53, 53, 40);
  border: 5px solid rgb(63,83,53);
  border-radius: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 10vmax;


}
.sidebar{
  border: 2px solid black;
  height:85vh;
  position: absolute;
  top:10vh;
  width:30vw;
  padding-top: 5vh;
  background-color: rgb(156,190,139);
  box-shadow: 20px 4px 20px rgb(53, 53, 40);
  overflow:scroll;
}

.card p{
  font-size: 2vmin;
  color: rgb(63,83,53) ;
  line-height: 100%;


}
.available{
  color: rgb(63,83,53);

}
.unAvailable{
  background-color: grey;
  color: rgb(169, 14, 14);

}

.resume :-webkit-any-link{
  text-decoration: none;
  text-align: center;
  color: rgb(63,83,53);
 
 }

.resume :any-link{
  text-decoration: none;
  text-align: center;
  color: rgb(63,83,53);
 
 } 
 .help{
   -webkit-animation: help 1s ;
           animation: help 1s ;
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
   position: fixed;
   bottom:0;
   right:0;
   z-index: 3;
 }
 @-webkit-keyframes help{
   0%{
    bottom:0;
    right:0;
  
   }
   50%{
    bottom: 3vh;
    right:0;
   }
   100%{
   
    bottom:0;
    right:0;
   }
 }
 @keyframes help{
   0%{
    bottom:0;
    right:0;
  
   }
   50%{
    bottom: 3vh;
    right:0;
   }
   100%{
   
    bottom:0;
    right:0;
   }
 }
.welcome{
    color:black;
    font-family: 'Homemade Apple', cursive;
    font-size: 3vmax;
    background-color:lightyellow;
    overflow: scroll;
    max-width:90vw;
    position: relative;
    z-index: 4;
    height:100vh;
    max-height: 100vh;
    border: 5px solid white;
    text-align: center;
    margin-top: 3vmin;
    padding:0vh 5vw;
 
}
#hello{
    font-size: 4vmax;
    -webkit-animation: hello 1s;
            animation: hello 1s;
   
}
.hireMe{
    -webkit-animation: hire 2s 1s 1 alternate;
            animation: hire 2s 1s 1 alternate;
    font-family: 'Codystar', cursive;
    height:-webkit-max-content;
    height:-moz-max-content;
    height:max-content;
    margin:3vmax 3vw;
    padding:0;
    border: 5px solid rgb(219, 255, 109);
    text-align: center;
    font-weight: bold;
    color: rgb(219, 255, 109);
    /* background-color: rgb(63,83,53); */
    /* text-shadow: 0px 0px 30px black; */
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgb(219, 255, 109);
    font-size: 3vmax;
    letter-spacing: 5px;
}

.closeMessage{
    margin:0;
    position:fixed;
    left:0;
    z-index: 2;
    font-size: 3vmax;
    color:rgb(169, 14, 14);
}

.creative{
    -webkit-animation: creative 10s infinite;
            animation: creative 10s infinite;
}

.moreDetails{
    /* background-color: rgb(116, 145, 169); */
    height:20vmax;
    border: 5px solid #799BB0;
    text-align: center;
    font-weight: bold;
    border-radius: 10px;
  
 
}

.moreDetails p{
    font-size: 3vmin;
  
}


@-webkit-keyframes hire{
    0%{transform: rotate(0deg);     

    }
    40%{transform: rotate(10deg);
 
    }
    80%{transform: rotate(-10deg)
    }
    100%{transform: rotate(0deg)
    }
}


@keyframes hire{
    0%{transform: rotate(0deg);     

    }
    40%{transform: rotate(10deg);
 
    }
    80%{transform: rotate(-10deg)
    }
    100%{transform: rotate(0deg)
    }
}

@-webkit-keyframes hello{
    0%{
      color: black;
      font-size: 3vmax;
    }
    50%{
        color: rgb(156,190,139);
        font-size: 6vmax;
   
    }
    100%{
        color: rgb(156,190,139);
        font-weight: bold;
        font-size: 6vmax;
    }
}

@keyframes hello{
    0%{
      color: black;
      font-size: 3vmax;
    }
    50%{
        color: rgb(156,190,139);
        font-size: 6vmax;
   
    }
    100%{
        color: rgb(156,190,139);
        font-weight: bold;
        font-size: 6vmax;
    }
}
@-webkit-keyframes creative{
  
    0%{
        color:green;
    }
   
    50%{
        color:orange;
    }
    100%{
        color:yellow;
    }
  
}
@keyframes creative{
  
    0%{
        color:green;
    }
   
    50%{
        color:orange;
    }
    100%{
        color:yellow;
    }
  
}


.About {
    height: 100vh;
    width:100vw;
    background-position: fixed;
    background-image: url(/static/media/wallpaperLeaves.aef8f7b6.jpg);
    background-size:100vmax;
    background-repeat: no-repeat;
    position: fixed;
}
.homeLink {
    -webkit-animation-name: homeLink ;
            animation-name: homeLink ;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    color:rgb(165,204,95);
    position: fixed;
    width:100vw;
    height:10vmax;
    background-color: black;
    bottom:0; 
    font-size: 5vmax;
    text-decoration: none;
    border-top: 3px solid grey;
    text-indent: 3vw;

}

.homeLink:hover{
    color:black;
    font-weight: bold;
    text-shadow: 0px 0px 5px rgb(165,204,95);
}

.rightArrow, .leftArrow{
    color:rgba(255, 255, 255, .8);
    font-size: 4vmax;
    margin:0;
    position: -webkit-sticky;
    position: sticky;
    z-index: 4;

}

.rightArrow{
    right:2vw;
}

.leftArrow{
   left:2vw;
}

.title{
    padding: 0vh 5vmax;
    
    }
    .title p{
        text-align: center;
        padding: 0;
    }
    
    .title:hover{
        text-shadow: 2px 2px 30px white ;
    }

    .titleCol{
        border-top: .5vh solid grey;
        border-bottom: .5vh solid grey;
        color:white;
        font-size:2vh;
        display:flex;
        width:100vw;
        overflow-x: scroll;
        position: fixed;
        top:10vh;
        z-index: 3;
        height:10vh;
       align-items: center;
       background-image: radial-gradient(rgb(128, 139, 150)   2%, black 80% );
    }

.banner{
    background-color: black;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    width:95vw;
    color:white;
    font-weight: bold;
    margin:0;
    padding:2vh 0vw;
    z-index: 4;
    font-size:3vmax;
    border-bottom: 3px solid grey;
}
.caption{
    position: -webkit-sticky;
    position: sticky;
    width:95vw;
    z-index:5;
    top: 0vh;
    text-align: center;
    background:rgba(0, 0, 0, .99);
    color:white;
    height:5vh;
    margin:0;
    padding:5vh 0vw;
    font-size: 2vw;
    text-indent: 5vw;
}
.closeView{
    margin:0;
    position:fixed;
    z-index: 6;
    top:20vh;
    left:1vw;
    font-size: 5vw;
    padding:1vh;
    color:rgb(169, 14, 14);
     
}


.detailedContent{

    overflow:scroll;
    padding-bottom: 5vmax;
    
}

.content{
    -webkit-animation: contentBox 1s;
            animation: contentBox 1s;  
    border: 3px solid darkgrey;
    position: absolute;
    top:20vh;
    z-index: 2;
    color:white;
    text-align: center;
    background-color: black;
    height:80vh;
    width:95vw;
    font-size: 5vmin;
}

.detailedContent img{
    width:60vw;
    margin:5vw 0;
    border: 2vw solid white;
}





@-webkit-keyframes homeLink{
    0%{
        margin-left: 100vw;
        background-color: unset;
        width:-webkit-max-content;
        width:max-content;
    }
    50%{
        width:100vw;
        /* transform: rotate(90deg); */
        transform: rotateY(180deg);
        text-shadow: 10px 10px 10px black;
        /* background-color: unset; */
      
        
    }80%{
        
    }
    100%{
    
       margin-left: 0vw;
  
    } 
}





@keyframes homeLink{
    0%{
        margin-left: 100vw;
        background-color: unset;
        width:-webkit-max-content;
        width:-moz-max-content;
        width:max-content;
    }
    50%{
        width:100vw;
        /* transform: rotate(90deg); */
        transform: rotateY(180deg);
        text-shadow: 10px 10px 10px black;
        /* background-color: unset; */
      
        
    }80%{
        
    }
    100%{
    
       margin-left: 0vw;
  
    } 
}
@-webkit-keyframes contentBox{
    from{margin-top: -100vh;
        

}
    to{margin-top:0;
       
     
    }
}
@keyframes contentBox{
    from{margin-top: -100vh;
        

}
    to{margin-top:0;
       
     
    }
}


  

/* HOVER EMOJIS */
.natureEmoji,.pizzaEmoji, .iceCreamEmoji, .puzzlesEmoji, .tacosEmoji, .familyEmoji, .raceCarEmoji, .craftsEmoji {
display:none;

}
 .nature:hover .natureEmoji, .pizza:hover .pizzaEmoji, .iceCream:hover .iceCreamEmoji, .puzzles:hover .puzzlesEmoji, .tacos:hover .tacosEmoji, .family:hover .familyEmoji, .raceCar:hover .raceCarEmoji, .crafts:hover .craftsEmoji {  
  display:inline-block;
  
}

